@import "variables";

.fbMarket {
  h1 {
    font-weight: 300;
  }
  .setup {
    a {
      color: $blue;
      text-decoration: underline;
      fill: $blue;
    }
    font-size: 0.95rem;
    display: grid;
    grid-gap: 1rem;
    margin: auto;
    max-width: 36rem;
    .btn {
      cursor: pointer;
      white-space: nowrap;
      margin: auto;
      margin-right: 0;
      padding: 0.5rem 0.8rem;
      background: $blue;
      color: white;
      font-weight: 600;
      border: 2px solid $blue;
      border-radius: 0.5rem;
      &:hover {
        background: white;
        color: $blue;
      }
    }
    .btn_clear {
      border: none;
      outline: none;
      background: none;
      color: $blue;
      cursor: pointer;
    }
    .profile {
      p {
        span {
          display: block;
          font-size: 0.8rem;
          color: rgba(0, 0, 0, 0.6);
        }
      }
    }
    .businessManager,
    .dataSharing,
    .page,
    .commerceAccount,
    .instagramAccount {
      ul {
        border: 1px solid rgba(0, 0, 0, 0.3);
        li {
          padding: 1.25rem 0.8rem;
          align-items: center;
          display: flex;
          grid-gap: 1rem;
          &:not(:last-child) {
            border-bottom: 1px solid rgba(0, 0, 0, 0.13);
          }
          .profile {
            .detail {
              a,
              p {
                display: block;
                margin-bottom: 0.3rem;
              }
              span {
                display: block;
                font-size: 0.8rem;
                color: rgba(0, 0, 0, 0.6);
              }
              a {
                width: max-content;
                display: flex;
                align-items: center;
                grid-gap: 0.3rem;
              }
            }
          }
        }
      }
    }
    .profile {
      grid-gap: 0.7rem;
    }
    .checkWrapper,
    .dataSharingLavel {
      display: flex;
      grid-gap: 0.7rem;
      align-items: center;
    }
    .dataSharingLavel {
      flex-wrap: wrap;
      .combobox {
        input {
          border-radius: 5px;
          border: 1px solid rgba(0, 0, 0, 0.2);
          padding: 0.4rem;
          width: 7rem;
        }
        button {
          right: 0.4rem;
        }
      }
      .dscr {
        flex-basis: 100%;
      }
    }
    .warning {
      padding: 1rem;
      border-radius: 0.5rem;
      display: flex;
      font-size: 0.9rem;
      grid-gap: 1rem;
      border: 1px solid rgba(232, 179, 22, 0.79);
      background: rgba(198, 152, 16, 0.07);
      svg {
        height: 2rem;
        width: 2rem;
        path {
          fill: rgb(230, 176, 18);
        }
      }
    }
    .pixel {
      p {
        font-size: 0.8rem;
        margin-bottom: 0.8rem;
      }
    }
    .btns {
      display: flex;
      grid-gap: 1rem;
      justify-content: flex-end;
      .btn {
        margin: 0;
      }
    }
    .commerceAccount {
      ul {
        .catalog {
          display: grid;
          grid-gap: 0.5rem;
        }
      }
    }
  }
}
.step {
  background: rgba(0, 0, 0, 0.01);
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 0.7rem;
  .head {
    padding: 1rem 1.3rem;
    display: flex;
    align-items: center;
    grid-gap: 0.75rem;
    font-weight: 500;
    .chev {
      width: 1rem;
      path {
        fill: rgba(0, 0, 0, 0.56);
      }
    }
    .status {
      margin: auto;
      margin-right: 0;
      svg {
        height: 1.2rem;
        width: 1.2rem;
      }
    }
  }
  .body {
    padding: 1.3rem;
    padding-top: 0.5rem;
    display: grid;
    grid-gap: 1.5rem;
    .note {
      font-size: 0.85rem;
      line-height: 1.5em;
      color: rgba(0, 0, 0, 0.8);
    }
    .user {
      align-items: center;
      display: flex;
      grid-gap: 0.7rem;
    }
    .err {
      border-radius: 0.5rem;
      background: rgba($red, 0.1);
      padding: 1rem;
      border: 1px solid $red;
      font-size: 0.9rem;
      a {
        color: inherit;
      }
    }
  }
  &.open {
    .head {
      .chev {
        transform: rotate(180deg);
      }
    }
  }
  &.disabled {
    opacity: 0.6;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.05);
    pointer-events: none;
  }
}

.createNewPage {
  font-size: 0.95rem;
  max-width: 40rem;
  .content {
    padding: 1.5rem;
    display: grid;
    grid-gap: 2rem;
    ul {
      display: grid;
      grid-gap: 0.5rem;
      margin-left: 2rem;
      li {
        list-style: initial;
      }
    }
    a {
      display: flex;
      align-items: center;
      grid-gap: 0.3rem;
      cursor: pointer;
      white-space: nowrap;
      margin: auto;
      margin-right: 0;
      padding: 0.5rem 0.8rem;
      background: $blue;
      color: white;
      font-weight: 600;
      border: 2px solid $blue;
      fill: white;
      border-radius: 0.5rem;
      &:hover {
        fill: $blue;
        background: white;
        color: $blue;
      }
    }
  }
}
